/* General Layout */
body {
  font-family: Arial, sans-serif;
  background: #101c3a;
  color: #fff;
  margin: 0;
  padding: 0;
}

header {
  text-align: center;
  padding: 1rem 0;
  background: #101c3a;
}

header h1 {
  margin: 0;
  font-size: 2.5rem;
}

nav ul {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center;
  gap: 20px;
}

nav ul li a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
}

nav ul li a:hover {
  text-decoration: underline;
}

/* About & Skills Layout */
#about-skills {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  gap: 20px;
}

.about-container,
.skills-container {
  flex: 1;
  background: #202940;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.about-container h2,
.skills-container h2 {
  color: #f4d03f;
  text-transform: uppercase;
  margin-bottom: 10px;
}

.skills-container .skill-card {
  background: #2d3b55;
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.skill-card h3 {
  margin: 0 0 5px;
  color: #f4d03f;
}
/* Timeline Section */
#timeline{
  padding: 20px;
}

#timeline h2, #contact h2 {
  color: #f4d03f;
  text-transform: uppercase;
  margin-bottom: 10px;
}

.timeline-item {
  margin-bottom: 15px;
  padding: 10px;
  background: #2d3b55;
  border-radius: 8px;
}

/* Contact Section */
.contact-container {
  background: #2d3b55;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  color: #fff;
  text-align: center;
}

.contact-container h2 {
  color: #f4d03f;
  text-transform: uppercase;
  margin-bottom: 15px;
}

.contact-container p {
  font-size: 1.1rem;
  margin-bottom: 15px;
}

.contact-links {
  display: flex;
  justify-content: center;
  gap: 40px; /* Space between links */
  margin-top: 15px;
}

.contact-links a {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #fff;
  font-weight: bold;
  font-size: 1rem; /* Larger text for better readability */
  transition: color 0.3s ease;
}

.contact-links a:hover {
  color: #f4d03f; /* Highlight color on hover */
}

/* Contact Links Container */
.contact-links img {
  width: 36px; /* Set the desired width */
  height: 36px; /* Set the desired height */
  object-fit: contain; /* Ensures the logo scales proportionally without distortion */
  margin-right: 10px; /* Adds spacing between the icon and the text */
  display: inline-block; /* Aligns the icons properly */
  border-radius: 4px; /* Optional: Rounded corners for consistency */
}

/* Portfolio Section */
#portfolio {
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
height: 100vh; /* Full page height */
background-color: #101c3a; /* Same as Home page */
color: #fff;
text-align: center;
}

#portfolio h2 {
color: #f4d03f; /* Yellow for consistency */
margin-bottom: 20px;
}

#portfolio p {
font-size: 1.2rem;
}

/* Footer */
footer {
  text-align: center;
  padding: 10px;
  background: #333;
  color: #fff;
  margin-top: 20px;
  font-size: 0.9rem;
}